<template>
  <div class="SignContainer">
    <el-form  ref="form" :model="form" :rules="signRules" label-width="120px" autocomplete="on">
      <el-form-item label="用户名" prop="username">
        <el-input ref="username" placeholder="用户名" v-model="form.user.username" />
      </el-form-item>
      <el-form-item label="密码"  prop="password">
        <el-input ref="mdfpassword" type="password" placeholder="密码" v-model="form.user.mdfpassword"  show-password/>
      </el-form-item>
      <el-form-item label="再次确认密码"  prop="mdfPassword">
        <el-input ref="password" type="password"  placeholder="再次确认密码" v-model="form.user.userpassword"  show-password/>
      </el-form-item>
      <el-form-item label="邮箱"  prop="email">
        <el-input ref="email" placeholder="**@163.com,***@qq.com" v-model="form.user.email" />
      </el-form-item>
      <el-form-item label="电话号码"  prop="phone">
        <el-input placeholder="请输入电话号码" v-model="form.user.phone" />
      </el-form-item>
      <el-form-item label="公司"  prop="company">
        <el-autocomplete
          v-model="form.company.name"
          :fetch-suggestions="querySearch"
          placeholder="输入公司名称"
          @select="handleSelect">
          <template #default="{ item }">
            <div>{{ item.name }}</div>
          </template>
          </el-autocomplete>
        <!-- <el-select v-model="form.companyId" placeholder="请选择所在的公司" >
          <el-option v-for="company in companies" :key="company.id" :label="company.name" :value="company.id"/> 
        </el-select> -->
      </el-form-item>
      <el-form-item label="超市名称"  prop="sname">
        <el-input placeholder="超市名称" v-model="form.supermark.sname" />
      </el-form-item>
      <el-form-item label="超市详细地址"  prop="sdetail">
        <el-input placeholder="超市详细地址" v-model="form.supermark.detail" />
      </el-form-item>
      <el-form-item label="地区"  prop="saddress">
        <addressSelect ref="addressSelect" />
      </el-form-item>
      <el-form-item label="证件上传"  prop="license">
        <el-upload
          ref="upload"
          class="upload-demo"
          :limit="1"
          :on-change="submitUpload"
          :auto-upload="false"
          
        >
        <template #trigger>
          <el-button type="primary">select file</el-button>
        </template>
        </el-upload>
      </el-form-item>
      <el-form-item >
        <el-button type="primary" @click="onSubmit">注册</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
  </template>
  
<script>
import addressSelect from '@/components/AddressSelect'
import {CompanyList} from '@/api/company'
import { ElMessage} from 'element-plus'
import Pagination from '@/components/Pagination' 
export default{
    // eslint-disable-next-line vue/multi-word-component-names
    name: "signup",
    components:{
      addressSelect,
      Pagination
    },
    created(){
      //加载公司列表
      this.getCompanyList()
    },
    mounted() {
    if (this.form.username === '') {
      this.$refs.username.focus()
    } else if (this.form.password === '') {
      this.$refs.password.focus()
    } else if (this.form.mdfPassword === ''){
      this.$refs.mdfPassword.focus()
    } else if (this.form.email == ''){
      this.$refs.email.focus()
    }
  },
    // computed:{
      
    // },
    data(){
      // const areaId = this.$refs.add.data.cityObj.code; 获取地区区号

      //用户名格式校验
    const validateUsername = (rule,value,callback) =>{
      if(this.form.user.username.trim() === ''){
        //函数回调
        callback(new Error('用户名不能为空'))
      }else{
        callback()
      }
    }
    //密码格式校验    
    const validatePassword = (rule,value,callback) =>{
      var pwd = this.form.user.mdfpassword
      if(pwd.trim() === '' || pwd.length < 6){
        //函数回调
        callback(new Error('密码不能为空且不得少于6个字符'))
      }else{
        callback()
      }
    }
    //确认密码格式校验
    const validateMdfPassword = (rule,value,callback) =>{
      if(this.form.user.userpassword != this.form.user.mdfpassword){
        //函数回调
        callback(new Error('两次输入的密码不一致'))
      }else{
        callback()
      }
    }
    
    //邮箱格式校验
    const validateEmail = (rule,value,callback) =>{
      var strRegex = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
      if(!strRegex.test(this.form.user.email)){
        //函数回调
        callback(new Error('邮箱格式错误'))
      }else{
        callback()
      }
    }
     
    //电话号码校验
    const validatePhone = (rule,value,callback) =>{
      if(this.form.user.phone.trim() == ''){
        callback(new Error("电话号码不能为空"))
      }else{
        callback()
      }
    }

    //公司校验
    const validateCompany = (rule,value,callback) =>{
      let flag = false;
      for(let i = 0; i < this.companies.length; i++){
        const comp = this.companies[i]
        if(comp.name == this.form.company.name) {
          this.form.company.id = comp.id
          flag = true;
          break
        }
      }
      if(!flag){
        callback(new Error("公司不存在"))
      }else{
        callback()
      }
    }

    //超市校验
    const validateSname = (rule,value,callback) =>{
      if(this.form.supermark.sname.trim() == ''){
        callback(new Error("超市名称不能为空"))
      }else{
        callback()
      }
    }

    //超市详细地址校验
    const validateSdetail = (rule,value,callback) =>{
      if(this.form.supermark.detail.trim() == ''){
        callback(new Error("电话号码不能为空"))
      }else{
        callback()
      }
    }

    //超市地区校验
    const validateAddress = (rule,value,callback) =>{
      if(typeof(this.$refs.addressSelect.data.cityObj.code) == 'undefined'){
        callback(new Error("地区不能为空"))
      }else{
        callback()
      }
    }

    //证件校验
    const validateLicense = (rule,value,callback) =>{
      if(Object.keys(this.form.supermark.license).length === 0){
        callback(new Error("证件信息不能为空"))
      }else{
        callback()
      }
    }
    return {
            companies : [],
            form:{
              user :{
                username: '',
                mdfpassword: '',
                userpassword: '',
                email: '',
                phone: '',
              },
              company: {
                id: '',
                name: '',
              },
              supermark:{
                sname: '',
                detail: '',
                address: '',
                license: {}
              } 
            },
            signRules:{
              username: [{required:true,trigger:'blur',validator: validateUsername}],
              password: [{required:true,trigger:'blur',validator: validatePassword}],
              mdfPassword : [{required:true,trigger:'blur',validator: validateMdfPassword}],
              email: [{required:true,trigger:'blur',validator: validateEmail}],
              phone: [{required:true,trigger:'blur',validator: validatePhone}],
              company : [{required:true,trigger:'blur',validator: validateCompany}],
              sname : [{required:true,trigger:'blur',validator: validateSname}],
              saddress : [{required:true,trigger:'blur',validator: validateAddress}],
              sdetail : [{required:true,trigger:'blur',validator: validateSdetail}],
              license: [{required:true,trigger:'blur',validator: validateLicense}],
            }
        }
    },
    methods : {
      submitUpload(file){
        this.form.supermark.license = file.raw
      //上传文件  
    },
    onSubmit(){
      //提交表单
      this.$refs.form.validate(valid => {
        if(valid){
          var pattern = /^([0-9a-zA-Z_\.\-\])+\@([0-9a-zA-Z_\.\-\])+\.([a-zA-Z]+)$/;
          if(this.form.user.mdfpassword != this.form.user.userpassword){
            ElMessage.error("两次输入的密码不同")
          }else if(!pattern.test(this.form.user.email)){
            ElMessage.error("邮箱格式错误，请重新输入")
          }else{
            this.form.supermark.address = this.$refs.addressSelect.data.cityObj.code //获取城市的区号
            console.info(this.$refs.upload);
            this.$store.dispatch('user/signUp',this.form)
            .then()
            .catch((err)=>{
            console.info(err)
            //提交表单异常
            // this.$router.push({path : '/message',query: '提交表单异常' })
          })
      }
        }
      })
      
      
    },
    handleExceed(){
      //处理文件
    },
    async getCompanyList(){
        let companies = {}
        await CompanyList()
        .then((response) => {
          companies = response.data
        })
        .catch()
        this.companies = companies
      },
      querySearch (queryString) {
        const list = JSON.parse(JSON.stringify(this.companies))
        const results = queryString
        ? list.filter(this.createFilter(queryString))
        : list
        return results
      },
      createFilter(queryString){
        return (company) => {
          return (
            company.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0
          )
        }
      },
      handleSelect(item){
        this.form.company = item
      }
    }
}
</script>

<style lang="scss" scoped>
.SignContainer{
  width:80%;
  text-align: center;
}
</style>